<template id="child-map">
  <div>
    <div id="map" :style="style"></div>
    <img id="image-class" src="../assets/tips.299ba2d9.png" class="image-class">
    <!-- <PolygonDB :map="map"></PolygonDB> -->
    <TelLogo></TelLogo>
    <div v-show="showDrawer" class="pop-info pop-box">
      <div v-for="item in drawerContent" :key="item.title" v-if="item.title === selectedCommunity">
        <div class="community msg-title">{{ item.title }}</div>
          <div class="msg-navigation" @click="chooseMap(item.title)"><img src="../assets/icon-arrow.png"></div>
            <div class="msg-intro">{{ item.intro }}</div>
            <div class="msg-info" v-for="hotline in item.label" :key="hotline.name">
            <div class="msg-info-title">{{ hotline.name }}</div>
                    <div class="msg-info-list" v-for="li in hotline.list" :key="li.tag">
                        <div>{{ li.tag }} 
                            <span>{{ li.man }} </span>
                            <a class="tel-link" href="tel:0724-2341647" data-tel="0724-2341647">{{ li.tel }}</a>
                        </div>
                    </div>
                </div>
            </div>
